                    <option value="agilent(PL)">agilent(PL)</option>
                    <option value="polytech消解仪">polytech消解仪</option>
                    <option value="polytech SPE">polytech SPE</option>
                    <option value="polytech 氮吹仪">polytech 氮吹仪</option>
                    <option value="polytech二噁英前处理仪">polytech二噁英前处理仪</option>
                    <option value="zoex">zoex</option>
                    <option value="other">other</option>
                  </select>
                  <div class="layui-unselect layui-form-select">
                    <div class="layui-select-title">
                      <input
