                        placeholder="请选择"
                        value
                        readonly
                        class="layui-input layui-unselect"
                      />
                      <i class="layui-edge"></i>
                    </div>
                    <dl class="layui-anim layui-anim-upbit">
                      <dd lay-value="J2 scientific" class>J2 scientific</dd>
                      <dd lay-value="OI analytical" class>OI analytical</dd>
                      <dd lay-value="agilent(PL)" class>agilent(PL)</dd>
                      <dd lay-value="polytech消解仪" class>polytech消解仪</dd>
                      <dd lay-value="polytech SPE" class>polytech SPE</dd>
                      <dd lay-value="polytech 氮吹仪" class>polytech 氮吹仪</dd>
                      <dd lay-value="polytech二噁英前处理仪" class>polytech二噁英前处理仪</dd>
                      <dd lay-value="zoex" class>zoex</dd>
                      <dd lay-value="other" class>other</dd>
                    </dl>
                  </div>
                </td>
              </tr>
              <tr>
                <td>服务请求</td>
                <td>
                  <select lay-verify="required" name="msg.serviceType">
                    <option value>请选择</option>
                    <option value="安装">安装</option>
                    <option value="维修">维修</option>
                    <option value="应用">应用</option>
