                    </div>
                    <dl class="layui-anim layui-anim-upbit">
                      <dd lay-value="安装" class>安装</dd>
                      <dd lay-value="维修" class>维修</dd>
                      <dd lay-value="应用" class>应用</dd>
                      <dd lay-value="搬家" class>搬家</dd>
                    </dl>
                  </div>
                </td>
              </tr>
              <tr>
                <td>问题类型</td>
